import './Form.css';
import React, {Component} from 'react';
import axios from 'axios';
import Global from '../Global';
import logo from '../../assets/images/metrolinea-logo.png';
import {io} from 'socket.io-client';
import Swal from 'sweetalert2';
import validator from 'validator';

class Form extends Component{
    
    
    
    userUrlStampRef = React.createRef();
    payroll = React.createRef();
    contractor = React.createRef();

    constructor(props){
        super(props);
        this.url = Global.url; 
        this.ws = Global.ws;
        this.socket = null;
        this.userTelephoneRef='';
        this.userIdentificationRef ='';
        this.userNameRef = '';
        this.userMailRef = '';
        this.userContentRef ='';
        this.mailForm= {};
        this.worngField='';
        this.state ={
            user : {
                name: '',
                email: '',
                identification: '',
                telephone: ''
            },
            stamp: null,
            form:{
                content:'',
                stamp_id:0,
                user_id:0
            },
            status: null,
            payroll: false,
            contract: false
        };
    }
    componentWillMount(){
        this.connectSocket();
    }
    fileChange =(e) =>{
        e.preventDefault();
        if(e.target.files[0].type!=='application/pdf'){
            Swal.fire(
                {
                    title: '<div class="big-square"></div>'+
                            '<div class="little-square"></div>'+
                            '<div class="bg-title-pop"><div class="list-label pop absolute-center"><p>El archivo adjunto debe ser formato pdf.</p></div></div>',
                            confirmButtonText: 'Seguir',
                            allowOutsideClick: false,
                            showCloseButton: true
                }
            )
            e.target.value= null;
            return;

        }
        var stampData = {
            stamp_url: e.target.files[0]
        }
        var object = new FormData();
        object.append('file',stampData.stamp_url)

        this.setState({
            stamp : object
        })
    }
    connectSocket = () =>{
        this.socket = (io(this.ws));
    }
    setSocket = (res) =>{
        //emit toast
        this.socket.emit("set-unchecked-news",res.data.data);
        this.socket.on("get-unchecked-news", data => {
            this.setState({
                forms: data,
                status: 'success'
            })
        });
    }
    getForms = async () =>{
       await axios.get(this.url+"get-unchecked-forms")
            .then( res => {
                this.setSocket(res);
            })
    }
    sendForm = (e) =>{
        e.preventDefault();
        var userData = {
            name : this.userNameRef,
            email : this.userMailRef,
            identification : this.userIdentificationRef,
            telephone : this.userTelephoneRef
        }
        this.setState({
           user : userData
        })
    }
    saveForm = (e) =>{
        e.preventDefault();
        var userData = {
            name : this.userNameRef,
            email : this.userMailRef,
            identification : this.userIdentificationRef,
            telephone : this.userTelephoneRef
        }
        this.setState({
           user : userData
        }, async () => {
            var formData = {
                content: this.userContentRef,
                user_id: 0,
                stamp_id: 0,
                request_type: this.payroll.current.checked ? 1 : 0
            }
            try {
                var validatorEmail= validator.isEmail(this.state.user.email) && validator.isLength(this.state.user.email,1,50);
                var validatorName= this.state.user.name!=='' && validator.isLength(this.state.user.name,1,50);
                var validatorTelephone= this.state.user.telephone!=='' && validator.isLength(this.state.user.telephone,1,16);
                var validatorIdentification= this.state.user.identification!=='' && validator.isLength(this.state.user.identification,1,16);
                var validatorPDF= this.state.stamp !== null;
                var validatorContent= formData.content !== '' && validator.isLength(formData.content,1,250);
                
            } catch (error) {
                
                Swal.fire(
                    {
                        title: '<div class="big-square"></div>'+
                                '<div class="little-square"></div>'+
                                '<div class="bg-title-pop"><div class="list-label pop absolute-center"><p>Formulario diligenciado correctamente. Recibirá un correo con más información sobre su petición.</p></div></div>',
                                confirmButtonText: 'Seguir',
                                allowOutsideClick: false,
                                showCloseButton: true
                    }
                )
                return console.log(error);
            }
            if(validatorEmail && validatorName && validatorContent && validatorTelephone && validatorIdentification && validatorPDF){
    
                axios.post(this.url+'create-user',this.state.user)
                .then( res =>{
                    if(res){
                        formData.user_id = res.data.data.insertId;
                        axios.post(this.url+'upload-stamp',this.state.stamp)
                        .then( async res =>{
                            if(res){
                                var stamp={
                                    stamp_url : res.data.data
                                }
                                await axios.post(this.url+'create-stamp',stamp)
                                .then( async res =>{
                                    if(res){
                                        formData.stamp_id = res.data.data.insertId;
                                        this.setState({
                                            form : formData
                                        })
                                        await axios.post(this.url+'create-form', this.state.form)
                                        .then( res =>{
                                            if(res){
                                                this.getForms();
                                                this.socket.emit('new-form',this.state.form);
                                                this.setState({
                                                    status:'succes'
                                                })
                                                Swal.fire({
                                                    title: '<div class="big-square"></div>'+
                                                    '<div class="little-square"></div>'+
                                                    '<div class="bg-title-pop"><div class="list-label pop absolute-center">Formulario diligenciado correctamente, recibirá un correo con más información sobre su petición.</div></div>',
                                                    confirmButtonText: 'Seguir',
                                                    allowOutsideClick: false,
                                                    showCloseButton: true
                                                }).then(()=>{
                                                    this.mailForm.email=this.state.user.email;
                                                    this.mailForm.name=this.state.user.name;
                                                    this.mailForm.mailState=2;
                                                    this.mailForm.id=res.data.data.insertId;
                                                    axios.post(this.url+"mail",this.mailForm)
                                                    .then( res => {
                                                        this.props.history.push('/');
                                                    });
                                                })
                                            }else{
                                                this.setState({
                                                    status:'failed'
                                                })
                                            }
                                        })
                                    }else{
                                        this.setState({
                                            status:'failed'
                                        })
                                    }
                                })
                            }else{
                                this.setState({
                                    status:'failed'
                                })
                            }
                        })
                    }else{
                        this.setState({
                            status:'failed'
                        })
                    }
                })
            }else{
                
                Swal.fire(
                    {
                        title: '<div class="big-square"></div>'+
                                '<div class="little-square"></div>'+
                                '<div class="bg-title-pop"><div class="list-label pop absolute-center"><p>Formulario mal diligenciado, debe ingresar un correo.</p></div></div>',
                                confirmButtonText: 'Seguir',
                                allowOutsideClick: false,
                                showCloseButton: true
                    }
                )
                return
            }
        });
        
        
    }
    backToNews = () => {
        this.props.history.push('/');
    }
    componentDidMount(){
        var elements = document.getElementsByTagName("INPUT");
        for (var i = 0; i < elements.length; i++) {
            elements[i].oninvalid = function(e) {
                e.target.setCustomValidity("");
                if (!e.target.validity.valid) {
                    if(e.target.name==='file0'){
                        e.target.setCustomValidity("Seleccione un archivo.");
                    }else{

                        e.target.setCustomValidity("Complete este campo.");
                    }
                }
            };
            elements[i].oninput = function(e) {
                e.target.setCustomValidity("");
            };
        }
        
        
    }
    render(){
       
        /*if(this.state.status==='succes'){
            return (
                <Redirect to="/user-home"/>
            )
        }*/
        return(
            <React.Fragment>
                <div className="form-content-container-main">
                    <form className="form-main" onSubmit={this.saveForm}>
                        <div className="form-group">
                            <div className="form-logo">
                                <img className="login-logo" src={logo} alt="imagen de metrolinea"></img>
                            </div>
                        </div>
                        <div className="form-group">
                            <div className="form-label" htmlFor="nombre">
                                <div className="text absolute-center">
                                    Nombre del solicitante
                                </div>
                                <div className="required-mark">
                                        *
                                    </div>
                            </div>
                            <input className="form-input" type="text" name="nombre" required maxLength="50" pattern="^[a-zA-Z ,.'-]+" onChange={(e)=>{
                                
                                this.userNameRef= e.target.value===''? e.target.value : this.userNameRef;
                                this.userNameRef= e.target.validity.valid? e.target.value : this.userNameRef;
                                e.target.value=this.userNameRef;
                                this.worngField= e.target.validity.valid? '': 'El campo nombre está mal diligenciado'
                            }}></input>
                        </div>
                        <div className="form-group">
                            <div className="form-label" htmlFor="mail">
                                <div className="text absolute-center">
                                    Correo
                                </div>
                                <div className="required-mark">
                                        *
                                    </div>
                            </div>
                            <input className="form-input" type="text" name="mail" required maxLength="50"  onChange={(e)=>{
                                this.userMailRef=  e.target.value===''? e.target.value : this.userMailRef;
                                this.userMailRef= e.target.validity.valid? e.target.value : this.userMailRef;
                                e.target.value=this.userMailRef;
                            }}></input>
                        </div>
                        <div className="form-group">
                            <div className="form-label" htmlFor="identification">
                                <div className="text absolute-center">
                                    Identificación
                                </div>
                                <div className="required-mark">
                                        *
                                    </div>
                            </div>
                            <input className="form-input" type="identification" name="identification" ref={this.userIdentificationRef} required maxLength="16" pattern= "\d+" onChange={(e)=>{
                                
                                this.userIdentificationRef=  e.target.value===''? e.target.value : this.userIdentificationRef;
                                this.userIdentificationRef= e.target.validity.valid? e.target.value : this.userIdentificationRef;
                                e.target.value=this.userIdentificationRef;
                                this.worngField= e.target.validity.valid? '': 'El campo identificación está mal diligenciado';
                            }}></input>
                        </div>
                        <div className="form-group">
                            <div className="form-label" htmlFor="telephone">
                                <div className="text absolute-center">
                                    Teléfono
                                </div>
                                <div className="required-mark">
                                        *
                                    </div>
                            </div>
                            <input className="form-input" type="telephone" name="telephone" required maxLength="16" pattern= "\d+" onChange={(e)=>{
                                
                                this.userTelephoneRef=  e.target.value===''? e.target.value : this.userTelephoneRef;
                                this.userTelephoneRef= e.target.validity.valid? e.target.value : this.userTelephoneRef;
                                e.target.value=this.userTelephoneRef;
                                this.worngField= e.target.validity.valid? '': 'El campo teléfono está mal diligenciado';
                            }}></input>
                        </div>
                        <div className="form-group form-text-area">
                            <div className="form-label" htmlFor="content">
                                <div className="text absolute-center">
                                    Descripción de la solicitud
                                </div>
                                <div className="required-mark">
                                        *
                                    </div>
                            </div>
                            <textarea className="text-area-input" type="text-area" name="content" required maxLength="250" pattern="^[a-zA-Z ,.'-]+" onChange={(e)=>{
                                
                                this.userContentRef=  e.target.value===''? e.target.value : this.userContentRef;
                                this.userContentRef= e.target.validity.valid? e.target.value : this.userContentRef;
                                e.target.value=this.userContentRef;
                                this.worngField= e.target.validity.valid? '': 'El campo contenido está mal diligenciado';
                            }}></textarea>
                        </div>
                        <div className="form-group">
                            <div className="form-label" htmlFor="fiel0">
                                <div className="text absolute-center">
                                    Adjuntar estampilla
                                </div>
                            </div>
                            <input id="archivo" className="form-input-file" type="file" name="file0" ref={this.userUrlStampRef} onChange={this.fileChange} required>
                                
                            </input>
                            <label className="file-input" htmlFor="archivo">
                                    Examinar
                            </label>
                            <div className="form-label">
                                <div className="text link absolute-center">
                                    Recuerda descargar tu estampilla primero <a href="https://estampillas.syc.com.co/"  target="_blank">Comprar estampilla</a>
                                </div>
                            </div>
                        </div>
                        <div className="form-group-radio">
                            <div className="form-label" htmlFor="requestType">
                                <div className="text-radio-label absolute-center">
                                    Tipo de petición:
                                </div>
                            </div>
                            <div className="row no-gutters select-type align-items-center justify-content-center">
                                <div className="col-lg-12 radio-input">
                                    <label className="container-radio">
                                        Carta laboral nómina
                                        <input type="radio" className="form-input-check"  name="requestType" ref={this.payroll} onChange={()=>{;this.payroll.current.checked=true;this.contractor.current.checked=false}} required></input>
                                        <span className="checkmark"></span>
                                    </label>  
                                </div>
                                <div className="col-lg-12 radio-input">    
                                    <label className="container-radio">
                                        Carta laboral contratista
                                        <input type="radio" className="form-input-check" name="requestType" ref={this.contractor} onChange={()=>{;this.payroll.current.checked=false;this.contractor.current.checked=true}}></input>
                                        <span className="checkmark"></span>
                                    </label>
                                </div>   
                            </div>
                            
                            
                        </div>
                        
                        <input type="submit" value="Enviar"></input>
                        <div className="back-container">
                            <div className="btn-back" onClick={this.backToNews}>
                                <div className="btn-label absolute-center">Volver</div>
                            </div>
                        </div>
                    </form>
                </div>
               
               
            </React.Fragment>
        )
    }
}
export default Form;